



















import {Component, Vue} from "vue-property-decorator"
import {TrainingSessionType} from "@chaosinsight/yogipeople-portalclient"
import Portal from "@/managers/portal"

@Component({ components: { } })
export default class UserTrainer extends Vue {

	private typeLabels: object = {
		hatha: TrainingSessionType.hatha,
		ashtanga: TrainingSessionType.ashtanga,
		vinyasa: TrainingSessionType.vinyasa,
		freeform: TrainingSessionType.freeform,
		yin: TrainingSessionType.yin
	}

	private typesSelected: TrainingSessionType[] = []
	private description: string | null = null

	/**
	 * GET trainer's Yoga Types
	 */

	private created(): void {
		Portal.client.call.trainer.me()
			.response
			.then(res => {
				this.typesSelected = [].slice.call(res.TrainingTypes)
				this.description = res.ProfileDescription
			})
	}

	/**
	 * UPDATE trainer's Yoga Types
	 */

	private postYogaTypes(): void {
		Portal.client.call.trainer.setTrainingTypes(this.typesSelected)
	}

	/**
	 * Api call to update trainer Description
	 */

	private postDescription(): void {
		if (!this.description)
			return


		Portal.client.call.profile.setDescription(this.description)
	}

	/**
	 * Capitalize word
	 */

	private capitalize(str: string): string {
		return str.charAt(0).toUpperCase() + str.slice(1)
	}
}
